.folder {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.5);
  transition: 0.2s opacity ease-in-out;
  button {
    background: none;
    border: none;
    cursor: pointer;
  }
  &:hover {
    opacity: 0.8;
  }
}
